html {
  scroll-behavior: smooth;
}

body {
  background: #151414;
  margin: 0;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Inter, source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

*,
::after,
::before {
  box-sizing: border-box;
}

*,
button,
input {
  font-family: 'Inter', serif;
  text-decoration: none;
  margin: 0;
  padding: 0;
}

html,
#root,
body {
  overflow-x: hidden;
}

h2 {
  font-family: 'Syne', 'Inter', serif !important;
}

/* *** BASIC COMMON CSS *** */
.pointer {
  cursor: pointer;
}

.not-allowed {
  cursor: not-allowed;
}

.tags {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 4px;
  padding: 2px 4px;
  color: #ffffff88;
}

.activeSidebar {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 2px;
  width: 100%;
  color: white !important;
}

.center {
  transform: translate(-50%, -50%);
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.links {
  color: #f47f57;
}

.asset-name {
  font-weight: 600;
  color: #f1f1f3;
}

@media (max-width: 1024px) {
  .hide-after-md {
    display: none;
  }
}

