/* *** MATERIALS *** */
.material-primary {
  position: relative;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(21px);
  border-radius: 6px;
  padding: 24px;
}

.material-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 6px;
  padding: 1px;
  background: linear-gradient(180deg, rgba(255, 116, 38, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  z-index: -1;
}

.material-secondary {
  background: linear-gradient(180deg, #48423e 0%, #373030 100%);
  box-shadow: 0 12px 24px -4px rgba(0, 0, 0, 0.12), 0 16px 20px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  width: 100%;
  padding: 32px;
}

@media (max-width: 600px) {
  .material-primary,
  .material-secondary {
    padding: 24px 24px;
  }

  .material-primary::before {
  }
}

.material-tertiary {
  background: linear-gradient(38.44deg, #f47f57 23.51%, #fd5656 69.95%);
  border-radius: 6px;
}

.material-quaternary {
  background: linear-gradient(180deg, #2d2d2d 0%, #1c1c1c 100%);
  border: 1px solid rgba(255, 255, 255, 0.16);
  box-sizing: border-box;
  border-radius: 6px;
  padding: 32px;
}

.custom-mahadao-container {
  background: linear-gradient(180deg, #48423e 0%, #373030 100%);
  border-radius: 6px;
}

.custom-mahadao-container-header {
  padding: 0 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.custom-mahadao-container-content {
  padding: 24px 32px 32px 32px;
}

@media (max-width: 600px) {
  .custom-mahadao-container {
    border-radius: 0;
  }

  .custom-mahadao-container-header {
    padding: 0 16px;
    /*border-radius: 0;*/
  }

  .custom-mahadao-container-content {
    padding: 24px 16px;
    /*border-radius: 0;*/
  }
}

/* *** SHADOWS *** */
.shadow-sm {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.shadow-md {
  box-shadow: 0 8px 16px -2px rgba(0, 0, 0, 0.12);
}

.shadow-lg {
  box-shadow: 0 12px 24px -4px rgba(0, 0, 0, 0.12), 0 16px 20px rgba(0, 0, 0, 0.25);
}

.shadow-xlg {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.6);
}

/* *** CORNER SYSTEM *** */
@media (max-width: 600px) {
  .mo-no-corner {
    border-radius: 0 !important;
  }
}

.corner-sm {
  border-radius: 2px;
}

.corner-md {
  border-radius: 4px;
}

.corner-lg-md {
  border-radius: 6px;
}

.corner-lg {
  border-radius: 8px;
}

.corner-xl {
  border-radius: 12px;
}

.corner-xxl {
  border-radius: 16px;
}

/* *** FLEX SYSTEM *** */
@media (max-width: 600px) {
  .mo-single-line-column {
    display: block !important;
  }
}

.single-line {
  display: flex;
  flex-direction: row;
}

.flex1 {
  flex: 1;
}

.single-line-center-start {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.start-center {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.single-line-center-center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.single-line-center-between {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.single-line-center-evenly {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.single-line-center-end {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.single-line-start-between {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}

.single-line-baseline-start {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
}

.single-line-baseline-between {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
}

.single-line-baseline-center {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
}

/* *** CONTAINER *** */
.custom-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto 0 auto;
  padding: 0 0 0 24px;
}

@media (max-width: 600px) {
  .custom-container {
    padding: 0;
  }

  .mo-custom-container {
    padding: 0 16px;
  }

  .border-container {
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
    border-right: none !important;
    margin-bottom: 14px !important;
    padding-bottom: 28px !important;
  }
}

.border-container {
  border-right: 1px solid rgba(255, 255, 255, 0.08);
}

/* *** DIVIDERS *** */
.bottom-divider {
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.top-divider {
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

.left-divider {
  border-left: 1px solid rgba(255, 255, 255, 0.08);
}

.right-divider {
  border-right: 1px solid rgba(255, 255, 255, 0.08);
}

/* *** ALIGNMENT *** */
.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

/* *** Custom *** */
.p-6 {
  padding: 6px !important;
}

/* width */
::-webkit-scrollbar {
  -webkit-appearance: none !important;
  width: 2px;
  height: 2px;
}

@media (max-width: 800px) {
  ::-webkit-scrollbar {
    width: 4px;
  }
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent !important;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffffff20 !important;
  border-radius: 10px !important;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: transparent !important;
}
