.SidebarContainer {
  width: 200px;
  background: #1f1e1e;
  border-radius: 6px;
  border: 1px solid;
  border-image-source: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  position: fixed;
  top: 0;
  padding: 16px;
  margin-top: calc(112px);
  min-height: calc(100vh - 144px);
}

.StyledLink {
  padding: 8px 16px;
  width: 100%;
  border-radius: 2px;
  display: block;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff88;
  margin-bottom: 8px;
}
.StyledLink:hover {
  outline: 1px solid rgba(255, 255, 255, 0.08);
  color: #ffffff88;
  opacity: 1;
}
.StyledLink:active {
  background: rgba(255, 255, 255, 0.01);
  border-radius: 2px;
  width: 100%;
  color: #ffffff88;
  opacity: 1;
}

.StyledLinkHref {
  padding: 8px 16px;
  width: 100%;
  border-radius: 2px;
  display: block;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
