.TopSideBar {
  padding-bottom: 177px;
}

.BottomSideBar {
  position: absolute;
  bottom: 0;
  padding: 16px 0;
  width: calc(100% - 32px);
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}
