.APR {
  display: flex;
  width: 100%;
  border-radius: 20px;
  background: #ffffff16;
}

.title {
  color: white;
  margin-bottom: 32px;
}

.apr-value {
  color: white;
  margin-bottom: 32px;
}

div[class*='apr-'] {
  width: 25%;
  text-align: center;
  padding: 12px;
  background: transparent;
  border-radius: 20px;
  color: #d2d2d2;
  cursor: pointer;
  transition: 0.5s ease;
}

div[class*='apr-']:hover {
  background: #fee2d5;
  opacity: 0.5;
  color: black;
}

.active-btn {
  background: #ff7f57 !important;
  color: white !important;
  font-weight: 600;
  /*box-shadow: 2px 2px 3px 0 rgb(215 215 215);*/
  transition: 0.5s ease;
}
